.tile {
  max-width: 400px;
  width: 100%;
  border-radius: 4px;
  box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.1);
  background-color: #fff;
  .tile-content {
    display: flex;
    flex-direction: column;
    .title {
      padding: 16px 24px 16px 24px;
      font-family: Merriweather, serif;
      font-size: 18px;
      font-weight: 400;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.56;
      letter-spacing: normal;
      color: #2f2f2f;
      word-wrap: break-word;
    }
    hr {
      width: 100%;
      height: 1px;
      background: #0000001a;
      border: none;
    }
    img {
      box-sizing: border-box;
      max-width: 100%;
      height: auto;
      margin: 16px 24px 0;
      border-radius: 4px;
    }
    .subtitle {
      padding: 16px 24px;
      font-family: Roboto, Lato, Arial;
      font-size: 14px;
      font-weight: 300;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.71;
      letter-spacing: normal;
      color: #343434;
      word-wrap: break-word;
    }
  }
}
