@import '~office-ui-fabric-react/dist/sass/References.scss';
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap');
.quickLinks {
  .container {
    max-width: 300px;
    margin: 0px auto;
    box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.1);
    padding: 0 20px;
  }

  .row {
    @include ms-Grid-row;
  }

  .column {
    @include ms-Grid-col;
    @include ms-lg12;
    @include ms-xl12;
    .content {
      width: 100%;
      .tilesHeader {
        display: flex;
        justify-content: space-between;
        margin-bottom: 26px;
        align-items: center;
        margin: 0 0 30px 0;
        h1 {
          font-size: 24px;
          font-weight: 300;
          font-stretch: normal;
          font-style: normal;
          line-height: 1.6;
          letter-spacing: 0.14px;
          color: #515150;
          margin: 0;
          font-family: 'Roboto', 'Segoe UI Web (West European)', Segoe UI,
            -apple-system, BlinkMacSystemFont, Roboto, Helvetica Neue,
            sans-serif;
        }
        a {
          font-family: 'Roboto', 'Segoe UI Web (West European)', Segoe UI,
            -apple-system, BlinkMacSystemFont, Roboto, Helvetica Neue,
            sans-serif;
          font-size: 14px;
          white-space: nowrap;
          -webkit-font-smoothing: antialiased;
          font-size: inherit;
          font-weight: inherit;
          color: #0078d4;
          outline: 0;
          text-decoration: none;
        }
      }
      .link {
        display: flex;
        align-items: center;
        margin-bottom: 15px;
        i {
          font-size: 20px;
        }
        a {
          margin-left: 16px;
          font-family: 'Roboto', 'Segoe UI Web (West European)', Segoe UI,
            -apple-system, BlinkMacSystemFont, Roboto, Helvetica Neue,
            sans-serif;
          font-size: 14px;
          font-weight: normal;
          font-stretch: normal;
          font-style: normal;
          line-height: 1.63;
          letter-spacing: normal;
          color: #0078d4;
        }
      }
    }
  }
}
