@import "~office-ui-fabric-react/dist/sass/References.scss";
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&display=swap");

.diagrams {
  font-family: "Montserrat", sans-serif;
  .container {
    max-width: 1280px;
    margin: 0px auto;
    @include ms-Grid;
  }

  .row {
    @include ms-Grid-row;
    display: flex;
    align-items: stretch;
  }

  .column {
    @include ms-Grid-col;
    @include ms-lg4;
    @include ms-xl4;
    @include ms-sm12;

    .content {
      min-height: 100%;
      background-color: rgba(255, 255, 255, 1);
      box-sizing: border-box;
      border: solid 1px rgba(215, 215, 215, 1);
      border-top: solid 4px #334f61;
      border-radius: 1px;
      padding: 15px;
      position: relative;
      .title {
        color: #333;
        font-weight: 400;
        font-style: normal;
        font-size: 20px;
        line-height: 22px;
        margin: 0 0 25px 0;
      }
      hr {
        height: 1px;
        background-color: rgba(215, 215, 215, 1);
        border: none;
        position: absolute;
        width: 100%;
        top: 45px;
        left: 0;
      }
      .contentItems {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 15px;
        h5 {
          color: #555;
          font-size: 15px;
          margin: 0 0 10px 0;
        }
        img {
          filter: opacity(50%);
          width: 100%;
          max-width: 218px;
        }
        .info {
          display: flex;
          justify-content: space-around;
          width: 100%;
          font-weight: 500;
          margin-top: 15px;
          p {
            color: #7f7f7f;
            font-size: 14px;
            line-height: 19px;
            margin: 5px 0 0 0;
            span {
              color: #333;
            }
          }
          p:first-child {
            margin-right: 15px;

          }
          p:last-child {
            margin-right: 0;
          }
        }
      }
    }
  }
}
