#tree {
  width: 100%;
  display: flex;
  justify-content: center;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  background: #f7f7f7;
}

.node-link {
  display: block;
  text-decoration: none;
}

.tree-children {
  display: flex;
  justify-content: space-around;
}

.tree-component {
  color: #000;
  background-color: #E5F3FA;
  width: 150px;
  min-height: 100px;
  word-break: break-word;
  margin: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  position: relative;
  box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.1);
}
.tree-component p,
.tree-component h4 {
  width: 100%;
}
.tree-component a {
  text-decoration: none;
  color: #000;
  width: 100%;
  height: 100%;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.tree-component a h4 {
  font-weight: 600;
  font-size: 1.3em;
  margin: 0;
}
.tree-component.clicked {
  background: #2c3890;
}
.tree-component.clicked a h4,
.tree-component.clicked a p {
  color: #fff;
}

.tree-node {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.tree-node.column {
  position: relative;
}
.tree-node.column .tree-component::before {
  display: block;
  content: "";
  position: absolute;
  width: 1px;
  height: 20px;
  background: #b5b6b7;
  top: -24px;
  left: calc(50% - 0.5px);
  z-index: 1;
}

.tree-children .tree-node:first-child.column > .tree-component::before {
  display: block;
  content: "";
  position: absolute;
  width: 1px;
  height: 12.5px;
  background: #b5b6b7;
  top: -15px;
  left: calc(50% - 0.5px);
  z-index: 1;
}
.tree-children .tree-node:first-child.column > .tree-component::after {
  display: block;
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background: #b5b6b7;
  top: -15px;
  left: calc(50% - 0.5px);
  z-index: 1;
}
.tree-children .tree-node:last-child.column > .tree-component::before {
  display: block;
  content: "";
  position: absolute;
  width: 1px;
  height: 12.5px;
  background: #b5b6b7;
  top: -15px;
  left: calc(50% - 0.5px);
  z-index: 1;
}
.tree-children .tree-node:last-child.column > .tree-component::after {
  display: block;
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background: #b5b6b7;
  top: -15px;
  right: calc(50% - 0.5px);
  left: auto;
  z-index: 1;
}

.root-node::after {
  display: block;
  content: "";
  position: absolute;
  width: 1px;
  background: #b5b6b7;
  height: 12.5px;
  bottom: -15px;
  left: calc(50% - 0.5px);
  z-index: 1;
}

.tree-node.column > .tree-component:first-child::before {
  display: block;
  content: "";
  position: absolute;
  width: 1px;
  height: 12.5px;
  background: #b5b6b7;
  top: -15px;
  left: calc(50% - 0.5px);
  z-index: 1;
}
.tree-node.column > .tree-component:first-child::after {
  display: block;
  content: "";
  position: absolute;
  width: 150%;
  height: 1px;
  background: #b5b6b7;
  top: -15px;
  left: 0;
  z-index: 1;
}